import React from 'react'
import { graphql } from 'gatsby'

import Layout from '../components/Layout'
import SEO from '../components/SEO'

import '../scss/bootstrap/bootstrap.scss'
import '../../content/assets/fonts/fonts.css'
import '../scss/app.scss'
import BannerRow from '../components/BannerRow'
import ContentRow from '../components/ContentRow'
import CalloutQuote from '../components/CalloutQuote'

import bannerImage from '../../content/assets/images/zak-sakata-7wBFsHWQDlk-unsplash.jpg'

class BlogIndex extends React.Component {
  render () {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO title='Home' />
        <BannerRow bgOpacity={0.5} bgImage={bannerImage}>
          We work with you to understand and communicate with data
          <br /> in an engaging and meaningful way.
        </BannerRow>
        <ContentRow rowClasses='pt-5'>
          <div className='card-deck'>
            <div className='card'>
              {/* <img src='...' className='card-img-top' alt='...' /> */}
              <div className='card-body'>
                <h5 className='card-title'>
                  A question you want to answer with data
                </h5>
                <p className='card-text'>
                  Do you have data and don’t know where to begin looking for
                  insight? We’ll work with you to develop a plan and find those
                  gems.
                </p>
              </div>
            </div>
            <div className='card'>
              {/* <img src='...' className='card-img-top' alt='...' /> */}
              <div className='card-body'>
                <h5 className='card-title'>The understanding of your data</h5>
                <p className='card-text'>
                  Do you need people within and outside your organization to
                  comprehend your data and its value? We’ll get people to engage
                  with it.
                </p>
              </div>
            </div>
            <div className='card'>
              {/* <img src='...' className='card-img-top' alt='...' /> */}
              <div className='card-body'>
                <h5 className='card-title'>
                  A message you need to share with data
                </h5>
                <p className='card-text'>
                  Do you want to improve how people visualize or interact with
                  your data? Our focus is user experience first.
                </p>
              </div>
            </div>
          </div>
        </ContentRow>
        <ContentRow>
          <CalloutQuote speaker='Adam La France, CEO of Knomos'>
            Working with Plot + Scatter has been a game-changer! They’ve taken
            everything that’s been swirling in our heads over the last year, and
            then have articulated and laid it out it in such a way that we have
            an actionable design roadmap. They’ve enabled us to reach our vision
            of using data viz to make law more accessible and useful for all
            audiences, rather than just paint pretty pictures.
          </CalloutQuote>
        </ContentRow>
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
